/* src/styles/styles.css - Enhanced FashionHub Styles */

/* --- RESET & BASICS --- */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  background-color: #f9fafb;
  color: #1f2937;
  line-height: 1.6;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

button {
  cursor: pointer;
  border: none;
  background: none;
  font-family: inherit;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  outline: none;
}

/* Smooth fade-in animation for page content */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* --- UTILITIES (with improvements) --- */
.min-h-screen {
  min-height: 100vh;
  animation: fadeIn 0.6s ease-out;
}

/* Background Colors */
.bg-white { background-color: white; }
.bg-gray-50 { background-color: #f9fafb; }
.bg-gray-100 { background-color: #f3f4f6; }
.bg-gray-200 { background-color: #e5e7eb; }
.bg-gray-800 { background-color: #1f2937; }
.bg-black-800 { background-color: #000000; }
.bg-green-600 { background-color: #00e06c; }
.bg-green-700 { background-color: #00ab53; }


/* Boder Colors */
.border-blue-600  { border-color: #0004ff; }
.border-gray-300 { border-color: #aeaeae; }



/* Text Colors */
.text-white { color: white; }
.text-gray-600 { color: #4b5563; }
.text-gray-700 { color: #374151; }
.text-gray-800 { color: #1f2937; }
.text-gray-400 { color: #9ca3af; }
.text-blue-600 { color: #3b82f6; }
.text-blue-500 { color: #2563eb; }
.text-red-500 { color: #ef4444; }
.text-green-600 {color: #00e06c; }
.text-purple-600 {color: #6a31ee; }

/* Typography */
.font-bold { font-weight: 700; }
.font-semibold { font-weight: 600; }
.text-center { text-align: center; }
.text-left { text-align: left; }
.text-xl { font-size: 1.25rem; line-height: 1.4; }
.text-2xl { font-size: 1.5rem; line-height: 1.3; }
.text-3xl { font-size: 1.875rem; line-height: 1.25; }
.text-4xl { font-size: 2.25rem; line-height: 1.2; }
.text-5xl { font-size: 3rem; line-height: 1.1; }
.text-sm { font-size: 0.875rem; }

/* Spacing */
.p-4 { padding: 1rem; }
.p-6 { padding: 1.5rem; }
.px-4 { padding-left: 1rem; padding-right: 1rem; }
.py-2 { padding-top: 0.5rem; padding-bottom: 0.5rem; }
.py-3 { padding-top: 0.75rem; padding-bottom: 0.75rem; }
.py-8 { padding-top: 2rem; padding-bottom: 2rem; }
.py-16 { padding-top: 4rem; padding-bottom: 4rem; }
.py-20 { padding-top: 5rem; padding-bottom: 5rem; }
.mt-2 { margin-top: 0.5rem; }
.mt-4 { margin-top: 1rem; }
.mt-8 { margin-top: 2rem; }
.mb-2 { margin-bottom: 0.5rem; }
.mb-4 { margin-bottom: 1rem; }
.mb-6 { margin-bottom: 1.5rem; }
.mb-8 { margin-bottom: 2rem; }
.mb-12 { margin-bottom: 3rem; }
.mr-3 { margin-right: 0.75rem; }

/* Sizing */
.w-full { width: 100%; }
.h-16 { height: 4rem; }
.h-64 { height: 30rem; }
.w-10 { width: 2.5rem; }
.w-12 { width: 3rem; }

/* Borders & Shadows */
.rounded { border-radius: 0.25rem; }
.rounded-lg { border-radius: 0.5rem; }
.rounded-full { border-radius: 9999px; }
.shadow { box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06); }
.shadow-md { box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06); }
.shadow-lg { box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05); }
.shadow-xl { box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04); }
.border { border: 1px solid #e5e7eb; }
.border-2 { border-width: 2px; }

/* --- FLEXBOX & GRID (Improved) --- */
.flex { display: flex; }
.flex-col { flex-direction: column; }
.items-center { align-items: center; }
.items-start { align-items: flex-start; }
.justify-center { justify-content: center; }
.justify-between { justify-content: space-between; }
.gap-2 { gap: 0.5rem; }
.gap-3 { gap: 0.75rem; }
.gap-4 { gap: 1rem; }
.gap-6 { gap: 1.5rem; }
.gap-8 { gap: 2rem; }

/* Grid */
.grid { display: grid; gap: 1.5rem; }
.grid-cols-1 { grid-template-columns: repeat(1, minmax(0, 1fr)); }

/* Responsive Grid */
@media (min-width: 768px) {
  .md\:grid-cols-2 { grid-template-columns: repeat(2, minmax(0, 1fr)); }
  .md\:grid-cols-4 { grid-template-columns: repeat(4, minmax(0, 1fr)); }
  .md\:flex { display: flex; }
  .md\:hidden { display: none; }
  .md\:space-x-8 > * + * { margin-left: 2rem; }
}

@media (min-width: 1024px) {
  .lg\:grid-cols-3 { grid-template-columns: repeat(3, minmax(0, 1fr)); }
  .lg\:grid-cols-4 { grid-template-columns: repeat(4, minmax(0, 1fr)); }
}

.max-w-7xl { max-width: 90rem; margin-left: auto; margin-right: auto; }
.max-w-4xl { max-width: 56rem; margin-left: auto; margin-right: auto; }
.mx-auto { margin-left: auto; margin-right: auto; }

/* --- ENHANCED NAVIGATION BAR --- */
nav.bg-white {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.08);
  border-bottom: 1px solid rgba(229, 231, 235, 0.5);
  position: sticky;
  top: 0;
  z-index: 100;
  animation: slideDown 0.4s cubic-bezier(0.16, 1, 0.3, 1);
}

@keyframes slideDown {
  from { transform: translateY(-100%); opacity: 0; }
  to { transform: translateY(0); opacity: 1; }
}

/* Brand Logo */
.text-2xl.font-bold.text-gray-800 {
  background: linear-gradient(135deg, #3b82f6, #9333ea);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-weight: 800;
  letter-spacing: -0.025em;
  transition: transform 0.3s ease;
  cursor: pointer;
}
.text-2xl.font-bold.text-gray-800:hover { transform: scale(1.05); }

/* Navigation Links */
.hidden.md\:flex.space-x-8 { gap: 2rem; }
.hidden.md\:flex.space-x-8 button {
  position: relative; font-weight: 500; color: #4b5563; padding: 0.5rem 0;
  background: none; border: none; transition: color 0.3s ease;
}
.hidden.md\:flex.space-x-8 button:hover { color: #3b82f6; }
.hidden.md\:flex.space-x-8 button::after {
  content: ''; position: absolute; bottom: 0; left: 50%; transform: translateX(-50%);
  width: 0; height: 2px; background: linear-gradient(to right, #3b82f6, #9333ea);
  border-radius: 2px; transition: width 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}
.hidden.md\:flex.space-x-8 button:hover::after { width: 100%; }

/* Mobile Menu Button */
.md\:hidden button {
  padding: 0.5rem; border-radius: 0.375rem; transition: all 0.3s ease;
}
.md\:hidden button:hover { background-color: #f3f4f6; }

/* Mobile Menu */
.md\:hidden + .md\:hidden { animation: slideIn 0.3s ease-out; }
@keyframes slideIn {
  from { opacity: 0; transform: translateY(-10px); }
  to { opacity: 1; transform: translateY(0); }
}
.md\:hidden + .md\:hidden button {
  padding: 0.75rem 1rem; width: 100%; text-align: left; border-bottom: 1px solid #f3f4f6;
  transition: all 0.2s ease;
}
.md\:hidden + .md\:hidden button:hover {
  background-color: #f8fafc; padding-left: 1.25rem; color: #3b82f6;
}
.md\:hidden + .md\:hidden button:last-child { border-bottom: none; }

/* --- ENHANCED HERO SECTION --- */
.bg-gradient-to-r {
  background: linear-gradient(135deg, #667eea 0%, #21159f 100%);
  position: relative;
  overflow: hidden;
  animation: gradientShift 8s ease infinite;
  background-size: 200% 200%;
}
@keyframes gradientShift {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}
.bg-gradient-to-r::before {
  content: ''; position: absolute; top: 0; left: 0; right: 0; bottom: 0;
  background: radial-gradient(circle at 20% 50%, rgba(255, 255, 255, 0.1) 0%, transparent 50%),
              radial-gradient(circle at 80% 20%, rgba(255, 255, 255, 0.1) 0%, transparent 50%);
  animation: float 20s ease-in-out infinite;
}
@keyframes float {
  0%, 100% { transform: translate(0, 0); }
  50% { transform: translate(50px, 50px); }
}

/* Hero Content */
.bg-gradient-to-r .text-center { position: relative; z-index: 1; }
.text-5xl.font-bold {
  text-shadow: 0 2px 20px rgba(0, 0, 0, 0.1);
  animation: fadeInUp 0.8s ease-out 0.2s both;
}
.text-xl { animation: fadeInUp 0.8s ease-out 0.4s both; }
@keyframes fadeInUp {
  from { opacity: 0; transform: translateY(30px); }
  to { opacity: 1; transform: translateY(0); }
}

/* Shop Now Button Enhancement */
.bg-white.text-blue-600 {
  background: white; color: #3b82f6; padding: 1rem 3rem; border-radius: 9999px;
  font-weight: 600; font-size: 1.125rem; position: relative; overflow: hidden; z-index: 1;
  box-shadow: 0 10px 30px rgba(59, 130, 246, 0.3);
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  animation: fadeInUp 0.8s ease-out 0.6s both;
}
.bg-white.text-blue-600::before {
  content: ''; position: absolute; top: 0; left: 0; right: 0; bottom: 0;
  background: linear-gradient(135deg, #3b82f6, #9333ea); opacity: 0; z-index: -1;
  transition: opacity 0.4s ease; border-radius: 9999px;
}
.bg-white.text-blue-600:hover {
  color: white; transform: translateY(-3px) scale(1.05);
  box-shadow: 0 20px 40px rgba(59, 130, 246, 0.4);
}
.bg-white.text-blue-600:hover::before { opacity: 1; }

/* ========================================================
   MODIFIED: PRODUCT CARDS (ZOOM INSIDE FRAME & IMAGE SWAP)
   ======================================================== */
.overflow-hidden.rounded-lg.shadow-lg {
  border-radius: 1rem;
  overflow: hidden; /* This is the mask */
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  background: white;
  position: relative;
}

.overflow-hidden.rounded-lg.shadow-lg::before {
  content: ''; position: absolute; top: 0; left: 0; right: 0; height: 4px;
  background: linear-gradient(to right, #3b82f6, #9333ea);
  transform: scaleX(0); transform-origin: left;
  transition: transform 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 10; /* Ensure line stays on top */
}

.overflow-hidden.rounded-lg.shadow-lg:hover::before { transform: scaleX(1); }
.overflow-hidden.rounded-lg.shadow-lg:hover {
  transform: translateY(-8px);
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
}

/* This targets the IMAGE inside the group */
.group:hover img {
  transform: scale(1.1); /* Zoom to 110% */
}

/* Product Card Text */
.text-lg.font-semibold {
  font-size: 1.125rem; font-weight: 600; color: #1f2937; transition: color 0.3s ease;
}
.group:hover .text-lg.font-semibold { color: #3b82f6; }

/* ======================================================== */

/* --- ENHANCED BUTTONS --- */
.bg-blue-600 {
  background: linear-gradient(135deg, #3b82f6, #2563eb);
  color: white; border-radius: 0.5rem; padding: 0.75rem 1.5rem; font-weight: 600;
  display: flex; align-items: center; justify-content: center; gap: 0.5rem;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0 10px 25px rgba(34, 83, 197, 0.3);
}
.bg-blue-600:hover {
  background: linear-gradient(135deg, #2563eb, #1d4ed8);
  transform: translateY(-2px); box-shadow: 0 10px 25px rgba(59, 130, 246, 0.3);
  box-shadow: 0 15px 35px rgba(34, 83, 197, 0.4);
}

.bg-green-500 {
  background: linear-gradient(135deg, #22c55e, #16a34a);
  color: white; border-radius: 0.75rem; padding: 1rem; font-weight: 600; font-size: 1.125rem;
  display: flex; align-items: center; justify-content: center; gap: 0.5rem;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0 10px 25px rgba(34, 197, 94, 0.3);
}
.bg-green-500:hover {
  background: linear-gradient(135deg, #22c55e, #458455);
  transform: translateY(-2px) scale(1.02);
  box-shadow: 0 15px 35px rgba(34, 197, 94, 0.4);
}

.bg-purple-600 {
  background: linear-gradient(135deg, #6622c5, #1f16a3);
  color: white; border-radius: 0.75rem; padding: 1rem; font-weight: 600; font-size: 1.125rem;
  display: flex; align-items: center; justify-content: center; gap: 0.5rem;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0 10px 25px rgba(34, 69, 197, 0.3);
}
.bg-purple-600:hover {
  background: linear-gradient(135deg, #2453c9, #3e4c7f);
  transform: translateY(-2px) scale(1.02);
  box-shadow: 0 10px 25px rgba(34, 69, 197, 0.4);
}

/* --- ENHANCED FORM ELEMENTS --- */
input[type="text"], input[type="email"], textarea {
  width: 100%; padding: 0.875rem 1rem; border: 2px solid #e5e7eb; border-radius: 0.5rem;
  font-size: 1rem; transition: all 0.3s ease; background: white;
}
input[type="text"]:focus, input[type="email"]:focus, textarea:focus {
  outline: none; border-color: #3b82f6;
  box-shadow: 0 0 0 4px rgba(59, 130, 246, 0.1); transform: translateY(-1px);
}

/* --- ENHANCED COLOR SELECTION --- */
button.px-4.py-2.border.rounded {
  padding: 0.5rem 1.5rem; border: 2px solid #e5e7eb; border-radius: 0.5rem;
  background: white; font-weight: 500; transition: all 0.3s ease;
}
button.px-4.py-2.border.rounded:hover {
  border-color: #3b82f6; color: #3b82f6; transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(59, 130, 246, 0.1);
}
button.px-4.py-2.border.rounded.bg-blue-600 {
  background: linear-gradient(135deg, #3b82f6, #2563eb); color: white; border-color: transparent;
  box-shadow: 0 5px 15px rgba(59, 130, 246, 0.2);
}
button.px-4.py-2.border.rounded.bg-blue-600:hover {
  background: linear-gradient(135deg, #2563eb, #1d4ed8); transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(59, 130, 246, 0.3);
}

/* --- QUANTITY SELECTOR --- */
.flex.items-center.gap-4 { display: flex; align-items: center; gap: 1rem; }
.bg-gray-200.w-10.h-10.rounded-full {
  background: white; border: 2px solid #e5e7eb; width: 2.5rem; height: 2.5rem; border-radius: 50%;
  display: flex; align-items: center; justify-content: center; font-weight: 700;
  font-size: 1.125rem; transition: all 0.3s ease;
}
.bg-gray-200.w-10.h-10.rounded-full:hover {
  background: #3b82f6; color: white; border-color: #3b82f6; transform: scale(1.1);
}

/* --- SECTION HEADERS --- */
.text-3xl.font-bold.text-center {
  position: relative; margin-bottom: 3rem; padding-bottom: 1rem;
}
.text-3xl.font-bold.text-center::after {
  content: ''; position: absolute; bottom: 0; left: 50%; transform: translateX(-50%);
  width: 60px; height: 4px; background: linear-gradient(to right, #3b82f6, #9333ea);
  border-radius: 2px; animation: expandWidth 0.8s ease-out 0.5s both;
}
@keyframes expandWidth { from { width: 0; } to { width: 60px; } }

/* --- SOCIAL ICONS --- */
.cursor-pointer.hover\:scale-110 { transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1); }
.cursor-pointer.hover\:scale-110:hover { transform: scale(1.2); }

/* --- FOOTER ENHANCEMENT --- */
footer.bg-gray-800 {
  background: linear-gradient(135deg, #1f2937, #111827); position: relative; overflow: hidden;
}
footer.bg-gray-800::before {
  content: ''; position: absolute; top: 0; left: 0; right: 0; height: 1px;
  background: linear-gradient(to right, transparent, #3b82f6, transparent);
}

/* --- SMOOTH SCROLLING & TRANSITIONS --- */
.transition-all { transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1); }
.transition-transform { transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1); }

/* Custom scrollbar */
::-webkit-scrollbar { width: 10px; }
::-webkit-scrollbar-track { background: #f1f1f1; }
::-webkit-scrollbar-thumb { background: linear-gradient(135deg, #3b82f6, #9333ea); border-radius: 5px; }
::-webkit-scrollbar-thumb:hover { background: linear-gradient(135deg, #2563eb, #7c3aed); }

/* --- LOADING STATES --- */
@keyframes pulse { 0%, 100% { opacity: 1; } 50% { opacity: 0.5; } }
.loading { animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite; }

/* --- RESPONSIVE IMPROVEMENTS --- */
@media (max-width: 768px) {
  .text-5xl.font-bold { font-size: 2.5rem; }
  .text-3xl.font-bold { font-size: 2rem; }
  .text-4xl.font-bold { font-size: 2.25rem; }
  .bg-white.text-blue-600 { padding: 0.875rem 2rem; font-size: 1rem; }
  .grid-cols-1 { gap: 1rem; }
  .md\:hidden + .md\:hidden { border-radius: 0 0 0.5rem 0.5rem; }
}

/* --- ACCESSIBILITY --- */
button:focus-visible, a:focus-visible {
  outline: 2px solid #3b82f6; outline-offset: 2px; border-radius: 0.25rem;
}

/* --- PRINT STYLES --- */
@media print {
  .bg-gradient-to-r, button, nav, footer { display: none !important; }
}

/* =========================================
   REQUIRED UTILITIES FOR IMAGE SWAP & ZOOM
   ========================================= */

/* 1. Layout helpers */
.relative { position: relative; }
.absolute { position: absolute; }
.inset-0 { top: 0; right: 0; bottom: 0; left: 0; }

/* 2. Shape helpers */
.rounded-none { border-radius: 0px; }
.rounded-2xl { border-radius: 1.5rem; }

/* 3. The Mask (Ensures no spillover) */
.overflow-hidden {
  overflow: hidden;
  position: relative;
  /* No transforms here */
}

/* 4. The Image (Moves inside the mask) */
.object-cover {
  object-fit: cover;
  width: 100%;
  height: 100%;
  display: block;
  /* Smooth Zoom Speed */
  transition: transform 0.6s ease-out, opacity 0.5s ease-in-out;
}

/* 5. Opacity States */
.opacity-0 { opacity: 0; }
.opacity-100 { opacity: 1; }
.transition-opacity { transition: opacity 0.5s ease-in-out; }

/* 6. Group Hover Logic */
/* Zoom the image when hovering the card */
.group:hover img {
  transform: scale(1.1);
}

/* Image Swap Fading */
.group:hover .group-hover\:opacity-0 { opacity: 0; }
.group:hover .group-hover\:opacity-100 { opacity: 1; }

/* =================================== */
/* CONSISTENT SMALL THUMBNAILS - CATEGORIES */
/* Matches exactly the product table style   */
/* =================================== */

.category-card .w-20.h-20.rounded-xl {
  width: 80px;
  height: 80px;
  border-radius: 16px;
  
}

.category-card .w-20.h-20 img {
  transition: transform 0.4s ease;
}

.category-card:hover .w-20.h-20 img {
  transform: scale(1.15);
}

/* Smooth card hover */
.category-card {
  transition: all 0.4s ease;
}

.category-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.15);
}

/* Truncate long text */
.line-clamp-2 {
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

/* ========================================================================== */
/* FINAL FIX: SHOW PRODUCT DETAILS IN SHOP PAGE (Name + Price + Description) */
/* ========================================================================== */

/* Product cards in Shop / Featured Products - now show full details */
.bg-white.rounded-none.shadow-lg.overflow-hidden {
  background: white;
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0,0,0,0.08);
  transition: all 0.5s ease;
  cursor: pointer;
  border: 2px solid #b1cfff;
  
}

.bg-white.rounded-none.shadow-lg.overflow-hidden:hover {
  transform: translateY(-10px);
  box-shadow: 0 30px 60px rgba(0,0,0,0.18);
}

/* Image */
.h-64.object-cover {
  height: 24rem;
  object-fit: cover;
  transition: transform 0.6s ease;
}

.bg-white.rounded-none.shadow-lg.overflow-hidden:hover img {
  transform: scale(1.08);
}

/* Text Content - Now VISIBLE and BEAUTIFUL */
.p-4 {
  padding: 1.5rem;
  background: white;
}

.p-4 h4 {
  font-size: 1.25rem;
  font-weight: 700;
  color: #1f2937;
  margin-bottom: 0.5rem;
  line-height: 1.4;
}

.text-gray-600.text-sm.mb-2 {
  color: #6b7280;
  font-size: 0.9rem;
  margin-bottom: 0.75rem;
  line-height: 1.5;
  display: block !important;
}

.text-blue-600.font-bold.text-xl {
  font-size: 1.5rem;
  font-weight: 800;
  color: #6366f1;
}

/* Make sure description doesn't disappear */
.text-gray-600.text-sm {
  opacity: 1 !important;
  visibility: visible !important;
}

