/* ==============================
   FashionHub Admin Panel - FINAL & COMPLETE CSS (2025)
   ============================== */

   @import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700;800;900&display=swap');

   /* --- 1. Global Reset --- */
   *, *::before, *::after {
     margin: 0;
     padding: 0;
     box-sizing: border-box;
   }
   
   body {
     font-family: 'Inter', system-ui, -apple-system, sans-serif;
     background: #f8fafc;
     color: #1e293b;
     line-height: 1.6;
   }
   
   /* --- 2. Login Page Styling --- */
   /* Targeting the gradient container */
   .bg-gradient-to-br {
     background: linear-gradient(135deg, #4b4eff 0%, #8b5cf6 50%, #e43c90 100%) !important;
   }
   
   .min-h-screen { min-height: 100vh; }
   .flex { display: flex; }
   .items-center { align-items: center; }
   .justify-center { justify-content: center; }
   
   /* Login Box */
   .bg-white.p-10.rounded-2xl {
     background: white;
     padding: 3rem;
     border-radius: 1.5rem;
     box-shadow: 0 30px 80px rgba(0, 0, 0, 0.3);
     width: 100%;
     max-width: 420px;
   }
   
   .text-3xl { font-size: 2.5rem; }
   .font-bold { font-weight: 900; }
   .text-center { text-align: center; }
   .mb-8 { margin-bottom: 2rem; }
   .mb-4 { margin-bottom: 1rem; }
   
   .text-red-600 { color: #dc2626; }
   
   /* --- 3. Inputs & Buttons --- */
   .space-y-6 > * + * { margin-top: 1.5rem; }
   
   .input-field {
     width: 100%;
     padding: 1rem 1.25rem;
     border: 2px solid #e2e8f0;
     border-radius: 1rem;
     font-size: 1.1rem;
     background: white;
     transition: all 0.3s ease;
     font-family: inherit;
   }
   
   .input-field:focus {
     outline: none;
     border-color: #6366f1;
     box-shadow: 0 0 0 5px rgba(99, 102, 241, 0.2);
     transform: translateY(-2px);
   }
   
   .btn-primary {
     background: linear-gradient(135deg, #6366f1, #8b5cf6);
     color: white;
     padding: 1rem;
     border: none;
     border-radius: 1rem;
     font-weight: 700;
     font-size: 1.1rem;
     cursor: pointer;
     transition: all 0.4s ease;
     box-shadow: 0 10px 30px rgba(99, 102, 241, 0.4);
     width: 100%; /* Default full width for login */
     display: flex;
     justify-content: center;
     align-items: center;
     gap: 0.5rem;
   }
   
   /* Adjust button width for non-login areas */
   button.btn-primary { width: auto; }
   
   .btn-primary:hover {
     transform: translateY(-4px);
     box-shadow: 0 20px 40px rgba(99, 102, 241, 0.5);
   }
   
   /* --- 4. Header & Layout --- */
   .shadow-lg { box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05); }
   .border-b { border-bottom: 1px solid #e5e7eb; }
   .bg-white { background-color: white; }
   
   .max-w-4xl { max-width: 80rem; margin: 0 auto; }
   .px-6 { padding-left: 1.5rem; padding-right: 1.5rem; }
   .py-5 { padding-top: 1.25rem; padding-bottom: 1.25rem; }
   .p-6 { padding: 1.5rem; }
   .justify-between { justify-content: space-between; }
   
   /* Gradient Text for Title */
   .bg-clip-text {
     background: linear-gradient(135deg, #6366f1, #a855f7);
     -webkit-background-clip: text;
     -webkit-text-fill-color: transparent;
     background-clip: text;
   }
   
   /* Logout Button */
   .text-red-600.font-semibold.border {
       border: 1px solid #fee2e2;
       background-color: white;
       color: #dc2626;
       transition: all 0.2s;
   }
   .text-red-600.font-semibold.border:hover {
       background-color: #fee2e2;
       border-color: #dc2626;
   }
   
   /* --- 5. Tabs --- */
   .flex.gap-4 { gap: 1rem; }
   .mb-8 { margin-bottom: 2rem; }
   .border-b-2 { border-bottom-width: 2px; }
   .border-gray-200 { border-color: #e5e7eb; }
   
   .tab-btn {
     padding: 0.75rem 1.5rem;
     font-weight: 600;
     color: #64748b;
     background: transparent;
     border: none;
     border-bottom: 3px solid transparent;
     display: flex;
     align-items: center;
     gap: 0.5rem;
     transition: all 0.3s ease;
     cursor: pointer;
     font-size: 1rem;
   }
   
   .tab-btn:hover {
     color: #6366f1;
     background: #f8fafc;
   }
   
   .tab-active {
     color: #6366f1;
     border-bottom-color: #6366f1;
   }
   
   /* --- 6. Tables & Lists --- */
   .bg-white.rounded-2xl.shadow-xl {
       border-radius: 1rem;
       box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
       overflow: hidden;
   }
   
   .w-full { width: 100%; }
   .w-small { width: 30%; }
   .text-left { text-align: left; }
   
   .bg-gradient-to-r.from-gray-50 {
       background: linear-gradient(to right, #f9fafb, #f3f4f6);
   }
   
   .table-th {
       padding: 1rem;
       font-size: 0.875rem;
       font-weight: 700;
       color: #4b5563;
       text-transform: uppercase;
       letter-spacing: 0.05em;
   }
   
   .table-td {
       padding: 1rem;
       vertical-align: middle;
       color: #1f2937;
       border-bottom: 1px solid #f3f4f6;
   }
   
   .border-b:last-child { border-bottom: none; }
   .hover\:bg-gray-50:hover { background-color: #f9fafb; }
   
   /* Product Images in Table */
   .w-16.h-16 { width: 4rem; height: 4rem; }
   .rounded-xl { border-radius: 0.75rem; }
   .object-cover { object-fit: cover; }
   .border-2 { border-width: 2px; }
   
   /* Status Badges */
   .rounded-full { border-radius: 9999px; }
   .text-xs { font-size: 0.75rem; }
   .bg-green-100 { background-color: #dcfce7; color: #166534; }
   .bg-red-100 { background-color: #fee2e2; color: #991b1b; }
   .px-3 { padding-left: 0.75rem; padding-right: 0.75rem; }
   .py-1 { padding-top: 0.25rem; padding-bottom: 0.25rem; }
   
   /* Action Buttons (Edit/Delete) */
   .icon-btn {
       width: 2.5rem;
       height: 2.5rem;
       border-radius: 0.5rem;
       display: inline-flex;
       align-items: center;
       justify-content: center;
       border: none;
       cursor: pointer;
       transition: all 0.2s;
   }
   
   .bg-blue-100 { background-color: #dbeafe; color: #1e40af; }
   .bg-blue-100:hover { background-color: #2563eb; color: white; }
   
   .bg-red-100.text-red-700 { background-color: #fee2e2; color: #b91c1c; }
   .bg-red-100:hover { background-color: #dc2626; color: white; }
   
   /* --- 7. Grid Layouts (Manual Tailwind) --- */
   .grid { display: grid; }
   .gap-6 { gap: 1.5rem; }
   .gap-8 { gap: 2rem; }
   .grid-cols-1 { grid-template-columns: repeat(1, minmax(0, 1fr)); }
   
   @media (min-width: 768px) {
       .md\:grid-cols-2 { grid-template-columns: repeat(2, minmax(0, 1fr)); }
       .md\:grid-cols-3 { grid-template-columns: repeat(3, minmax(0, 1fr)); }
       .md\:grid-cols-4 { grid-template-columns: repeat(4, minmax(0, 1fr)); }
   }
   
   /* --- 8. Category Cards --- */
   .category-card .group > div {
       transition: all 0.3s ease;
       border: 1px solid #f3f4f6;
   }
   .category-card:hover > div {
       transform: translateY(-5px);
       box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
       border-color: #bfdbfe; /* blue-200 */
   }
   .truncate {
       overflow: hidden;
       text-overflow: ellipsis;
       white-space: nowrap;
   }
   
   /* --- 9. MODAL STYLES --- */
   .fixed.inset-0 { position: fixed; top: 0; right: 0; bottom: 0; left: 0; }
   .bg-black.bg-opacity-60 { background-color: rgba(0, 0, 0, 0.6); backdrop-filter: blur(4px); }
   .z-50 { z-index: 50; }
   
   .bg-white.rounded-3xl {
       background-color: white;
       border-radius: 1.5rem;
       box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
   }
   
   .max-w-4xl { max-width: 56rem; }
   .max-h-\[90vh\] { max-height: 90vh; }
   .overflow-y-auto { overflow-y: auto; }
   
   /* Form Labels */
   .label-required {
       display: block;
       font-weight: 600;
       margin-bottom: 0.5rem;
       color: #374151;
   }
   .label-required::after {
       content: " *";
       color: #ef4444;
   }
   
   /* File Inputs - Creative & Attractive */
   .file-btn {
       padding: 0.75rem;
       border: 2px dashed #cbd5e1;
       border-radius: 1rem;
       background: #f8fafc;
       width: 100%;
       cursor: pointer;
       transition: all 0.3s ease;
       display: flex;
       align-items: center;
       position: relative;
       overflow: hidden;
   }
   
   .file-btn:hover {
       border-color: #6366f1;
       background: #eef2ff;
       box-shadow: 0 4px 12px rgba(99, 102, 241, 0.15);
       transform: translateY(-2px);
   }
   
   .file-btn::file-selector-button {
       background: linear-gradient(135deg, #6366f1, #8b5cf6);
       color: white;
       padding: 0.6rem 1.25rem;
       border: none;
       border-radius: 0.75rem;
       margin-right: 1rem;
       cursor: pointer;
       font-weight: 700;
       font-family: inherit;
       font-size: 0.9rem;
       box-shadow: 0 4px 6px rgba(99, 102, 241, 0.3);
       transition: all 0.3s ease;
   }
   
   .file-btn:hover::file-selector-button {
       background: linear-gradient(135deg, #4f46e5, #7c3aed);
       transform: scale(1.05);
       box-shadow: 0 6px 12px rgba(99, 102, 241, 0.4);
   }
   
   /* Textarea */
   .resize-none { resize: none; }
   
   /* Modal Footer Buttons */
   .bg-gray-200 { background-color: #e5e7eb; color: #1f2937; }
   .bg-gray-200:hover { background-color: #d1d5db; }

  .p-4 { padding: 1rem; }
  .p-6 { padding: 1.5rem; }
  .p-8 { padding: 3rem; }